                  :rules="[v => !!v || 'Selecione o Tipo de Problema']"          
                  :items="items"
                  item-text="text"
                  item-value="value"
                >
                </v-select>
                <v-text-field v-model="Ocorrencia.NomeZona" label="Bairro" required></v-text-field>
                <v-text-field v-model="Ocorrencia.NomeRua" label="Rua" required></v-text-field>
                <v-textarea
                  clearable
                  auto-grow
                  rows="3"
                  dense
                  v-model="Ocorrencia.InfoAdicionas"
                  name="input-7-4"
                  label="Informações Adicionais"
                ></v-textarea>
              </v-col>
            </v-row>
            <v-row>
              <v-layout row wrap>  
                <v-flex xs6 class="hello-text">
