                    <div slot="activator">
                      <v-avatar size="164px" v-ripple v-if="!avatar" class="mb-3 profile">
                        <span>Click para Carregar Imagem</span>
                      </v-avatar>
                      <v-avatar 
                        color="grey"
                        size="100%"
                        tile v-ripple v-else class="mb-3 profile">
                        <img :src="avatar.imageURL" alt="avatar">
                      </v-avatar> 
                    </div>
                  </input-di-imagem>
                </v-flex>
