</template>

<script>
import axios from 'axios';
import Mapbox from "mapbox-gl";
import firebase from '../ConfigFirebase.js'
import InputDiImagem from '../components/InputDiImagem.vue'
import registraOcorrencia from '../services/registraOcorrencia.js'
const moment = require('moment')
require('moment/locale/pt')

export default {
    name: 'Home',
    components: {
      InputDiImagem: InputDiImagem
    },
    data() {
        return {
            valid: true,
            fab: false,
            show: true,
            FABDESATIVADO: false,
            imageData1:'',
            avatar: null,
            avatar2: null,
            avatar3: null,
            saving: false,
            saved: false,
            imageData2:'',
            imageData3:'',
            tooltips: false,
            ToolTipBT: true,
            mediaStream: null,
            center: [],
            dialog: false,
            dialogKamera: false,
            tooltipsDisabled: false,
            overlay: false,
            Ocorrencia: {
                TipuIcon: "",
                STADU: "PROBLEMA",
                TamanhoIcon: [40, 40],
                DataRegistro: new Date().getTime(),
                kordenadas: [-23.512842, 14.923609],
                TipoOcorrencia: "",
                NomeRua: "",
                NomeZona: "",
                InfoAdicionas: "",
                Imagem1: null,
            },
            DadosCoordenadas: [],
            TipoProblemaSelecionado: null,
            items: [
                {text: 'Acessos para Cidadãos com Mobilidade Reduzida', value: 'PIN_ACESSO_PARA_DEFICIENTES.png'},
                {text: 'Animais Abandonados', value: 'PIN_ABANDONO_ANIMAIS2.png'},
                {text: 'Conservação da Iluminação Pública', value: 'PIN_ILUMINASON_PUBLICA2.png'},
                {text: 'Conservação das Ruas e Estrada', value: 'PIN_ESTRADA.png'},
                {text: 'Estacionamento de Veículos', value: 'PIN_ESTACIONAMENTO_CARRO.png'},
                {text: 'Limpeza de Valetas, Bermas e Caminhos', value: 'PIN_VALETAS.png'},
                {text: 'Limpeza e Conservação de Espaços Públicos', value: 'PIN_ESPACO_PUBLICO.png'},
                {text: 'Manutenção de Ciclovias', value: 'PIN_CICLIVIA.png'},
                {text: 'Manutenção e Limpeza de Contentores', value: 'PIN_CONTENTORES.png'},
                {text: 'Manutenção, Rega e Limpeza de Praças', value: 'PIN_PRACA.png'},
                {text: 'Nomes ou Numeração de Ruas', value: 'PIN_NOME_RUA.png'},
                {text: 'Poluição Sonora', value: 'PIN_POLUISON_SONORA.png'},
                {text: 'Publicidade, Outdoors e Cartazes', value: 'PIN_PUBLICIDADE.png'},
                {text: 'Recolha de Lixo', value: 'PIN_RECOLHA_LIXO.png'},
                {text: 'Saneamento, Ruturas de Águas ou Desvio de Tampas', value: 'PIN_SANEAMENTO.png'},
                {text: 'Sinalização de Trânsito', value: 'PIN_SINAIS_TRANSITO.png'}
            ],
            accessToken: process.env.VUE_APP_MAPBOX_TOKEN,
            mapStyle: 'mapbox://styles/mapbox/streets-v11',
            // mapData: "https://raw.githubusercontent.com/johan/world.geo.json/master/countries.geo.json",
            latitude: 14.907233,
            longitude: -23.514992,
            zoom: 14,
            bearing: 0,
            pitch: 0,
            bgImage: "@/assets/tomb-512.png"
        };
    },
    created() {
        // limpa elemento mapa
        this.map = null;
    },
    methods: {
      addMarkers(map, bounds) {
        let self = this;
        firebase.db.on('value', function(ocorrencias) {
          let returnArr = [];
          ocorrencias.forEach((ocorrencia) => {
              const markerEl = document.createElement('div');
              markerEl.innerHTML = '<img width="50px" src="/img/'+ocorrencia.val().STADU+'/'+ocorrencia.val().TipuIcon+'">';
              markerEl.style.cursor = 'pointer';
                var COR_H3
                if (ocorrencia.val().STADU === "PROBLEMA") {
                  COR_H3 = "#d80027";
                } else if (ocorrencia.val().STADU === "EM_ANALISE") {
                  COR_H3 = "#3298fd";
                } else if (ocorrencia.val().STADU === "RESOLVENDO") {
                  COR_H3 = "#eda22b";
                } else {
                  COR_H3 = "#4e9a06";
                }
              new Mapbox.Marker(markerEl, { offset: [12,-10] }).setLngLat(ocorrencia.val().kordenadas)
                  .setPopup(new Mapbox.Popup({ offset: 25 })
                  .setHTML('<h3 style="background: '+COR_H3+';">'+ocorrencia.val().TipoOcorrencia+'</h3><p><strong>Registrado em:</strong><br>'+ moment(ocorrencia.val().DataRegistro).format('DD [de] MMMM [de] YYYY')+' ('+moment(ocorrencia.val().DataRegistro).fromNow()+')<br><strong>Bairro:</strong><br>'+ocorrencia.val().NomeZona+'<br><strong>Rua:</strong><br>'+ocorrencia.val().NomeRua+'<br><strong>Descricão do Problema:</strong><br>'+ocorrencia.val().InfoAdicionas+'</p>'))
                  .addTo(self.map);
              bounds.extend(ocorrencia.val().kordenadas);
              markerEl.addEventListener('click', () => {
                  self.map.flyTo({
                      center: ocorrencia.val().kordenadas,
                      zoom: 17,
                  });      
              });    
              
                self.ocorrencias = returnArr;  
          });
        })
      },
      fitBounds(map, bounds) {
          /** 
           * Load event: https://www.mapbox.com/mapbox-gl-js/api#map.event:load
           * Fired immediately after all necessary resources have been downloaded and
           * the first visually complete rendering of the map has occurred. 
          **/
            console.log(map)
            console.log(bounds)
      },
      post(){
          this.$refs.form.validate()
            registraOcorrencia(this.Ocorrencia)
            this.$swal('Ocorrência Registrado com sucesso.')
            this.dialog = false;
            this.fab = false;
            this.tooltipsDisabled = false;
            this.Ocorrencia.TipuIcon = "";
            this.Ocorrencia.STADU = "PROBLEMA";
            this.Ocorrencia.TamanhoIcon = [40, 40];
            this.Ocorrencia.DataRegistro = new Date().getTime();
            this.Ocorrencia.kordenadas = [-23.512842, 14.923609];
            this.Ocorrencia.TipoOcorrencia = "";
            this.Ocorrencia.NomeRua = "";
            this.Ocorrencia.NomeZona = "";
            this.Ocorrencia.InfoAdicionas = "";
            this.Ocorrencia.Imagem1 = null;
      },
      uploadImage() {
        this.saving = true
        setTimeout(() => this.savedAvatar(), 1000)
      },
      savedAvatar() {
        this.saving = false
        this.saved = true
      },      
      PanhaNhaLocalizason() {      
        let self = this;  
        if (navigator.geolocation) {
          navigator.geolocation.getCurrentPosition(displayLocationInfo);
        }
        function displayLocationInfo(position) {
        console.log(position)
          const lng = position.coords.longitude;
          const lat = position.coords.latitude;
          self.map.flyTo({
              center: [lng, lat],
              zoom: 15,
          });
          // self.NomeBairro = `longitude: ${ lng } | latitude: ${ lat }`          
          axios.get(`https://api.mapbox.com/geocoding/v5/mapbox.places/${ lng },${ lat }.json?limit=1&access_token=${ self.accessToken }`)
          .then(response => {
            // JSON responses are automatically parsed.
            // this.posts = response.data
            console.log(response.data.features[0].text)
            console.log(response.data.features[0].place_name)
            console.log(response.data.features[0].geometry.coordinates)
            self.Ocorrencia.NomeZona=response.data.features[0].text
            self.Ocorrencia.kordenadas=response.data.features[0].geometry.coordinates
          })
          .catch(e => {
            this.errors.push(e)
          })
        }
      },
      PanhaLocalizasonNaMapa() {

        this.fab = false;
        this.FABDESATIVADO = true
        let self = this;
        var popup = new Mapbox.Popup({ offset: 25 }).setHTML(
        '<br><p style="text-align: center;"><strong>Arraste para o local do problema.</strong></p>'
        );
        var coordinates = document.getElementById('coordinates');
        var marker = new Mapbox.Marker({
        draggable: true
        })
        .setLngLat(self.map.getCenter())
        .setPopup(popup)
        .addTo(self.map)
        .togglePopup();
        
        function onDragEnd() {
        var lngLat = marker.getLngLat();
        coordinates.style.display = 'block';
        coordinates.innerHTML =
        'Longitude: ' + lngLat.lng + '<br />Latitude: ' + lngLat.lat;
        self.dialog = true;
        self.overlay = true;
        self.fab = false;
        self.FABDESATIVADO = false;
        marker.remove();
        // console.log('Longitude: ' + lngLat.lng + 'Latitude: ' + lngLat.lat);
          axios.get(`https://api.mapbox.com/geocoding/v5/mapbox.places/${ lngLat.lng },${ lngLat.lat }.json?limit=1&access_token=${ self.accessToken }`)
          .then(response => {
            self.Ocorrencia.NomeZona=response.data.features[0].text
            self.Ocorrencia.kordenadas=lngLat
            self.overlay = false;

          })
          .catch(e => {
            this.errors.push(e)
          })
        }        
        marker.on('dragend', onDragEnd);
        self.dialog = false;
      },
      GEOlocaliZASON(){    
        let self = this;    
        this.map.addControl(new Mapbox.NavigationControl());
/*          this.map.addControl(
            new Mapbox.GeolocateControl({
                positionOptions: {
                    enableHighAccuracy: false
                },
                trackUserLocation: true
            })
        );  */
        if (navigator.geolocation) {
          navigator.geolocation.getCurrentPosition(displayLocationInfo);
        }
        function displayLocationInfo(position) {
          const lng = position.coords.longitude;
          const lat = position.coords.latitude;
          self.map.flyTo({
              center: [lng, lat],
              zoom: 14,
          });
          // console.log(`longitude: ${ lng } | latitude: ${ lat }`);
        }
      }
    },
    destroyed () {
    },
    mounted() {
        // pa inicializa mapa
        this.map = new Mapbox.Map({
            accessToken: this.accessToken,
            container: this.$refs.map,
            style: this.mapStyle ,
            center: [this.longitude, this.latitude],
            zoom: this.zoom,
            pitch: this.pitch,
            bearing: this.bearing
        });
         
        const bounds = new Mapbox.LngLatBounds();
        
        this.addMarkers(this.map, bounds);  
        this.fitBounds(this.map, bounds);
        this.GEOlocaliZASON();

    },

  watch: {
    avatar: {
      handler: function() {
        this.saved = false
      },
      deep: true
    },
    TipoProblemaSelecionado(val){
        this.Ocorrencia.TipoOcorrencia = val.text
        this.Ocorrencia.TipuIcon = val.value
    },
    fab (val) {
      this.tooltips = false
      this.tooltipsDisabled = false
      val && setTimeout(() => {
        this.tooltips = true
        this.$nextTick(() => this.tooltipsDisabled = false)
        // console.log("sta funciona")
      }, 250)
    }
  }
}
</script>

